import React, {
  ReactNode,
  FC
} from 'react';
import { css } from '@emotion/react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
}

const Button: FC<ButtonProps> = ({
  children,
  onClick
}: ButtonProps) => {
  const styles = css({
    padding: '10px',
    border: '1px solid #000',
  });
  return (
    <button
      css={styles}
    >
      {children}
    </button>
  )
}

export default Button;
